import React from "react"
import PropTypes from "prop-types"


const Header = ({ siteTitle }) => (
  <header>
    <span>{siteTitle}</span>    
    <a href="mailto:info@bowcraftworks.com" target="_top"><span>info@bowcraftworks.com</span> </a>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}




export default Header
